import { DirektoriLisensiTipeService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { Lisensi_tipe } from 'src/app/sdk/hr/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { Router, ActivatedRoute } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriLisensiTipeService: DirektoriLisensiTipeService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private router: Router,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute
    ) { }

    data: Lisensi_tipe

    ngOnInit() {
        this.id= this.activatedRoute.snapshot.paramMap.get('id');
        this.form();
        this.getData()
    }

    id: string
    getData() {
        this.spinner.show()
        this.direktoriLisensiTipeService.getDirektoriLisensiTipesId(this.id).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }

    dataForm: FormGroupTypeSafe<Lisensi_tipe>;
    form() {
        this.dataForm = this.fb.group<Lisensi_tipe>(
            {
                id_lisensi_tipe: new FormControl('', [Validators.required]),
                tipe_lisensi: new FormControl(''),
                keterangan: new FormControl(''),
                lama_berlaku_val: new FormControl(''),
                lama_berlaku_var: new FormControl(''),
                nama_lisensi: new FormControl(''),
                status: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.direktoriLisensiTipeService.putDirektoriLisensiTipesId({
            id: this.id,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pengaturan/jlisensi/list'])
    }

}
